body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.default-layout{
    display:grid;
    grid-template-rows: 80px auto 3rem;
    grid-template-areas:
        "header"
        "main"
        "footer"
}

header{
    grid-area: header;
}
main{
    grid-area: main;
    min-height: 88vh;
}
footer{
    grid-area: footer;
}
.jumbotron {
    padding: 2rem 1rem;
    margin-bottom: 2rem;
    background-color: #e9ecef;
    border-radius: .3rem;
    max-width: 550px;
    margin-left: auto;
    margin-right:auto;
    box-shadow: 0px 0px 15px -5px black;
}

.Eventt {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
}
.eventList {
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    grid-gap: 20px;
    gap: 20px;
}

.eventListItem {
    flex: 1 1;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.eventListImg {
    width: 100%;
    height: 300px;
    border-radius: 10px;
}

    .eventListImg:hover {
        text-decoration: none;
        color: white;
        box-shadow:0px 0px 15px -5px slategrey;
    }

